<!--  -->
<template>
  <div>
    <Zi bool1="true" goback="true" data="<" con="修改用户名">
      <h1 @click="$router.go(-1)" slot="left"><</h1>
    </Zi>
    <div class="cont">
        <input v-model="val" type="text" name="" id="">
        <p>用户名只能修改一次（5-24字符之间）</p>
        <button @click="add">确认修改</button>
    </div>
  </div>
</template>

<script>
import Zi from "../../components/header";
export default {
  data() {
    return {
        val:"",
        user_id:""
    };
  },

  components: { Zi },

  computed: {},

  mounted() {
    // if(localStorage.getItem("user_id")){
    //  this.user_id=JSON.parse(localStorage.getItem("user_id"))
    // }
  },

  created() {},


  methods: {
   add(){
        this.$store.state.username=this.val
        this.$router.push("/Info")
   
   }
      }
    
    }
  
</script>
<style scoped lang="scss">
.cont{
    padding-left: 0.2rem;
    p{
        line-height: 0.5rem;
        height: 0.5rem;
    }
    button{
            display: block;
    width: 92%;
    background: none;
    line-height: 0.7rem;
    font-size: .3rem;
    color: #fff;
    // opacity: .6;
    transition: all 1s;
    background: #3190e8
    }
}
input{
    margin-top: 1rem;
    display: block;
        background: none;
    width:90%;
    border: 1px solid #ddd;
    border-radius: 2px;
    padding: .2rem .1rem;
    line-height: 0.4rem;
    display: block;
}


</style>


